export const sonority = [ "obstuent", "nasal", "liquid", "glide", "vowel"]

const stop = ["p","b","t","d", "ʈ", "ɖ", "c", "ɟ", "k", "ɡ", "q", "ɢ", "ʔ"]
const fricative = ["ɸ","β","f","v","θ", "ð", "s", "z", "ʃ", "ʒ","ʂ","ç", "x", "ɣ", "χ", "ʁ", "h"]
const affricate = ["t͡s", "d͡z", "t͡ʃ", "d͡ʒ", "ʈ͡ʂ", "c͡ç", "ɟ͡ʝ"]
const nasal = ["m", "n", "ɳ", "ɲ", "ŋ"]
const flap = ["ɾ", "ɽ"]
const trill = ["r"]
const lateral = ["l", "ɭ", "ʎ"]
const approximant = ["w", "ɹ", "ɻ", "j"]
const vowel = ["i","y","ɨ","ɯ", "u", "ɪ", "ʊ", "e", "o", "ə", "ɛ", "ʌ", "ɔ", "æ","a", "ɑ"]

export const keyboard = stop.concat(fricative, affricate, nasal, flap, trill, lateral, approximant, vowel)

const sonority_dict = {
    "vowel" : vowel,
    "glide" : ["j", "w"],
    "liquid" : ["ɾ", "ɽ", "r", "l", "ɭ", "ʎ", "ɹ", "ɻ"],
    "nasal" : nasal,
    "obstuent" : stop.concat(fricative, affricate),
}

const segment_to_sonority = (function () {
    const dict = {}
    for (let i = 0; i < sonority.length; i++) {
        let s = sonority[i]
        let l = sonority_dict[s]
        for (let seg of l){
            dict[seg] = i
        } 
    }
    return dict
})()

export function get_indexes(segment_list){
    let res = []
    for (let s of segment_list){
        res.push(segment_to_sonority[s])
    }
    return res
}

export function get_syllables(segment_list){
    let res = []
    for (let s of segment_list){
        res.push("T")
    }
    let indexes = get_indexes(segment_list)
    let prev = -1
    let i = 0
    let rise = false
    let rise_idx = -1
    // get nucleus
    while (i < indexes.length){
        let next = i + 1 < indexes.length ? indexes[i + 1] : -1
        
        let idx = indexes[i]
        if (idx > next && idx > prev){
            if (segment_list[i] == "j" || segment_list[i] == "w"){
                return "Invalid Input. Glides are never syllabic in any language."
            }
            res[i] = "N"
        }
        else if(idx > prev && idx === next){
            rise = true
            rise_idx = i 
        }
        else if(rise && idx > next){
            rise_idx = rise_idx != -1 ? rise_idx : 0
            for (let j = rise_idx; j < i + 1; j++){
                if (segment_list[i] == "j" || segment_list[i] == "w"){
                    return "Invalid Input. Glides are never syllabic in any language."
                }
                res[j] = "N"
            }
        }

        if (idx != next){
            rise = false
        }
        prev = indexes[i]
        i += 1
    }

    
    // get unknown
    i = 0
    let falling = false
    let falling_id = -1
    prev = -1
    while (i < indexes.length){
        let next = i + 1 < indexes.length ? indexes[i + 1] : -1
        
        let idx = indexes[i]
        if (idx < next && idx < prev){
            res[i] = "?"
        }
        else if(idx < prev && idx === next){
            falling = true
            falling_id = i 
        }
        else if(idx > next){
            falling = false
        }
        else if(falling && idx < next){
            falling_id = falling_id != -1 ? falling_id : 0
            for (let j = falling_id; j < i + 1; j++){
                res[j] = "?"
            }
        }
        prev = indexes[i]
        i += 1
    }

    i = 0
    let foundN = false
    let lastSpecial = "N"
    // get onset
    while(i < indexes.length){
        if (res[i] === "N"){
            lastSpecial = "N"
            foundN = true
            i += 1
            continue
        }else if (res[i] === "?"){
            lastSpecial = "?"
            i += 1
            continue 
        }

        if (!foundN){
            res[i] = "O"
            i += 1
            continue
        }

        if (lastSpecial == "?"){
            res[i] = "O"
            i += 1
            continue
        }

        if (lastSpecial == "N"){
            res[i] = "C"
            i += 1
            continue
        }

        i += 1
    }

    i = 0
    while(i < indexes.length){
        let next = i + 1 < indexes.length ? res[i + 1] : null
        if (next === "N" && res[i] === "?"){
            res[i] = "O"
        }
        i += 1
    }

    let j = indexes.length - 1
    while(j >= 0){
        if (res[j] == "N"){
            break
        }
        res[j] = "C"
        j -= 1
    }

    return res
}