import './keyboard.css';

import React from 'react';
import { keyboard } from './checker';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';

const COLUMNS = 12;

export class Keyboard extends React.Component {

    constructor(props) {
        super(props);
        this.rows = [];
        let i = 0;
        while (i < keyboard.length) {
            this.rows.push(keyboard.slice(i, Math.min(i + COLUMNS, keyboard.length)));
            i += COLUMNS;
        }
    }

    onKeyboardClick(text) {
        this.props.onKeyClicked(text);
    }

    render() {
        return (
        <Paper elevation="2" style={{"width": "600px", "marginTop": "50px", "backgroundColor": "#323542"}}>
                <table style={{"width": "100%", "padding": "15px", "tableLayout": "fixed"}}>
                <tbody>
                {this.rows.map(row => {
                    return (
                    <tr>
                    {row.map(item => {
                        return (
                            <th>
                            <div className="keyboard-button" onClick={() => this.onKeyboardClick(item)}>
                                {item}
                            </div>
                            </th>
                        )
                    })}
                    </tr>
                    )
                })}
            </tbody>
            </table>
        </Paper>
        )
    }
}
