import './App.css';

import React from 'react';
import { ThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import { Keyboard } from './keyboard';
import { keyboard, get_indexes, get_syllables } from './checker';
import Snackbar from '@material-ui/core/Snackbar';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';

// twɛlfθs spɹɪnt ɡɑɹdnz ɪkspɛnsɪv

function linearMap(inRangeStart, inRangeEnd, outRangeStart, outRangeEnd, value) {
		return outRangeStart + (value - inRangeStart) * (outRangeEnd - outRangeStart) / (inRangeEnd - inRangeStart);
}

const MAX_CHARS_IN_IPA = 3;

const ipaSet = (() => {
    const result = new Set();
    keyboard.forEach(ipa => {result.add(ipa)});
    return result;
})()

const theme = createMuiTheme({
  palette: {
    type: "dark",
  }
});

class AnalysisDescription extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            allowDiphthongs: false,
        }
    }

    renderSplit(split) {
        return (<p>
                <span style={{fontWeight: 'bold', marginRight: '6px', color: "#ffbc82"}}>{split.length}</span>
                {split.length == 1 ? 'syllable' : 'syllables'}:
                <span style={{marginLeft: '20px'}}>[{split.join('.')}]</span>
        </p>)
    }

    render() {
        const data = this.props.data[this.state.allowDiphthongs ? 'withDiphthongs' : 'withoutDiphthongs'];

        return (<div style={{marginBottom: '35px'}}>
            <div style={{fontWeight: 'bold', marginBottom: '10px'}}>
                <FormControlLabel
                    control={
                    <Checkbox
                        checked={this.state.allowDiphthongs}
                        onChange={(e) => this.setState({allowDiphthongs: e.target.checked})}
                        name="checkedB"
                        color="primary"
                    />
                    }
                    label="Allow Diphthongs"
                />
            </div>
            {data.length == 1 ?
             this.renderSplit(data[0]) :
            (<div>
                <div className="description-text">
                If all ambiguities are treated as onset:
                </div>
                {this.renderSplit(data[0])}
                <div className="description-text">
                If all ambiguities are treated as coda:
                </div>
                {this.renderSplit(data[1])}
                <div className="description-text">
                Other syllabifications are possible depending on how the ambiguities are interpreted.
                </div>
            </div>)
            }
        </div>);
    }
}

class App extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            text: "",
            showResults: false,
            errorMessage: null,
        };
        this.parsedInputToDraw = null;
    }

    changeText(text) {
        this.setState({ text: text });
    }

    onKeyboardClicked(text) {
        this.setState({ text: this.state.text + text });
    }

    onSearch() {
        const text = this.state.text;
        this.setState({ showResults: true });

        let inputValid = false;

        const parsedInput = this.parseInput(text);

        if (typeof parsedInput === "string") {
            this.setState({ errorMessage: parsedInput });
        } else {
            this.setState({ errorMessage: null });
            this.parsedInputToDraw = parsedInput;
            this.analysisData = this.getAnalysisData(parsedInput);
        }
    }

    getWithAmbiguityResolved(input, i, ambiguity) {
        const s = input[i].syllable;
        return s == '?' ? ambiguity : s;
    }

    getAnalysisData(input) {
        const data = {};
        for (let allowDiphthongs of [false, true]) {
            const splits = [];
            for (let ambiguity of ['O', 'C']) {
                const split = [];
                const nucleusList = [];
                let lastNucleusStart = -1;
                for (let i = 0; i < input.length; i++) {
                    const {text, index, syllable} = input[i];
                    if (!allowDiphthongs || syllable != 'N') {
                        if (lastNucleusStart >= 0) {
                            nucleusList.push([lastNucleusStart, i - 1]);
                            lastNucleusStart = -1;
                        }
                    }
                    if (syllable == 'N') {
                        if (lastNucleusStart < 0) {
                            lastNucleusStart = i;
                        }
                    }
                }
                if (lastNucleusStart >= 0) {
                    nucleusList.push([lastNucleusStart, input.length - 1]);
                }

                for (let i = 0; i < nucleusList.length; i++) {
                    let [start, end] = nucleusList[i];
                    start--;
                    end++;
                    while (start >= 0 && this.getWithAmbiguityResolved(input, start, ambiguity) == 'O')
                        start--;
                    while (end < input.length && this.getWithAmbiguityResolved(input, end, ambiguity) == 'C')
                        end++;
                    start++;
                    end--;
                    split.push(input.slice(start, end + 1).map(item => item.text).join(''))
                }
                splits.push(split);
            }
            if (input.every(item => item.syllable != '?')) splits.pop();

            data[allowDiphthongs ? 'withDiphthongs' : 'withoutDiphthongs'] = splits;
        }
        return data;
    }

    parseInput(text) {
        const segments = [];
        let i = 0;
        while (i < text.length) {
            let found = false;
            for (let j = Math.min(MAX_CHARS_IN_IPA, text.length - i); j >= 1; --j) {
                const slice = text.slice(i, i + j);
                if (ipaSet.has(slice)) {
                    segments.push(slice);
                    i += j;
                    found = true;
                    break;
                }
            }
            if (!found) {
                return "Invalid input. Please try again.";
            }
        }
        const indices = get_indexes(segments);
        const syllables = get_syllables(segments);
        if (typeof syllables === 'string') return syllables;
        const result = [];
        for (let i = 0; i < indices.length; i++) {
            result.push({
                text: segments[i],
                index: indices[i],
                syllable: syllables[i],
            });
        }
        return result;
    }

    componentDidUpdate() {
        if (!this.parsedInputToDraw) return;
        const input = this.parsedInputToDraw;
        this.parsedInputToDraw = null;

        const canvas = this.refs.canvas;
        if (!canvas) return;

        const widthPerChar = 50;
        const margin = [120, 30];
        const yDrawRegion = [50, 170];
        const labelY = 200;
        const syllableToColor = {
            "O": "#00FF00",
            "N": "#00feff",
            "C": "#FF0000",
            "?": "#FFFF00",
        }
        const backgroundColor = "#222327"

        const data = []
        for (let i = 0; i < input.length; i++) {
            const pointData = input[i];
            const x = margin[0] + (i + 0.5) * widthPerChar;
            const y = linearMap(4, 0, yDrawRegion[0], yDrawRegion[1], pointData.index);
            const text = pointData.text;
            const s = pointData.syllable;
            data.push({x, y, text, s});
        }

        canvas.width = widthPerChar * input.length + margin[0] + margin[1];
        canvas.height = 250;
        const ctx = canvas.getContext('2d');
        ctx.fillStyle = backgroundColor;
        ctx.fillRect(0, 0, canvas.width, canvas.height);

        ctx.strokeStyle = "#666666";
        ctx.strokeRect(0, 0, canvas.width, canvas.height);

        // y label
        ctx.textAlign = "center";
        ctx.textBaseline = 'middle';
        ctx.font = "15px Arial";
        let x = null, y = null;
        ctx.fillStyle = "#ffffff";
        x = margin[0] / 2;
        y = linearMap(4, 0, yDrawRegion[0], yDrawRegion[1], 0);
        ctx.fillText("Obstruent", x, y);
        y = linearMap(4, 0, yDrawRegion[0], yDrawRegion[1], 1);
        ctx.fillText("Nasal", x, y);
        y = linearMap(4, 0, yDrawRegion[0], yDrawRegion[1], 2);
        ctx.fillText("Liquid", x, y);
        y = linearMap(4, 0, yDrawRegion[0], yDrawRegion[1], 3);
        ctx.fillText("Glide", x, y);
        y = linearMap(4, 0, yDrawRegion[0], yDrawRegion[1], 4);
        ctx.fillText("Vowel", x, y);

        ctx.beginPath();
        ctx.strokeStyle = "#ffffff";
        for (let i = 0; i < data.length; ++i) {
            const {x, y} = data[i];
            if (i == 0) {
                ctx.moveTo(x, y);
            } else {
                ctx.lineTo(x, y);
            }
        }
        ctx.stroke();

        ctx.font = "bold 15px Arial";
        ctx.textAlign = "center";
        ctx.textBaseline = 'middle';
        const clearSize = 16;
        for (let i = 0; i < data.length; ++i) {
            const {x, y, s} = data[i];
            ctx.fillStyle = backgroundColor;
            ctx.fillRect(x - clearSize / 2, y - clearSize / 2, clearSize, clearSize);
            ctx.fillStyle = syllableToColor[s];
            ctx.fillText(s, x, y);
            // ctx.beginPath();
            // ctx.arc(x, y, 5, 0, 2 * Math.PI);
            // ctx.stroke();
            // ctx.fill();
        }

        ctx.fillStyle = "#ffffff";
        ctx.font = "17px Arial";
        ctx.textAlign = "center";
        ctx.textBaseline = 'top';
        for (let i = 0; i < data.length; ++i) {
            const {x, y, text} = data[i];
            ctx.fillText(text, x, labelY);

            ctx.beginPath();
            ctx.setLineDash([2, 8]);
            ctx.moveTo(x, y + 10);
            ctx.lineTo(x, labelY - 5);
            ctx.stroke();
        }
    }

    backToSearch() {
        this.setState({ showResults: false });
    }

    render() {
        return (
            <ThemeProvider theme={theme}>
            <div className="column-flex">
            <header className="App-header">
                <h1>
                Syllable Analyzer
                </h1>
            </header>
            {
            this.state.showResults ?
            <div className="column-flex">
                <div className="text-to-analyze">
                    [{this.state.text}]
                </div>
                {this.state.errorMessage ? <div className="invalid-input-error">
                    {this.state.errorMessage}
                 </div> :
                 (
                    <>
                    <canvas ref="canvas" width={300} height={300} className="canvas"/>
                         <AnalysisDescription data={this.analysisData}></AnalysisDescription>
                    </>
                 )
                }
                <Button variant="contained"
                    onClick={() => {this.backToSearch()}}>
                    Back
                </Button>
            </div>
            :
            <div className="column-flex">
            <div className="search-bar-container">
                <TextField className="search-bar" label="Enter transcription in IPA" variant="outlined"
                value={this.state.text}
                onKeyPress={(e) => {if (e.charCode === 13) this.onSearch()}}
                onChange={(e) => {this.changeText(e.target.value)}}/>
            <Button variant="contained" style={{"marginLeft": "10px", "height": "50px"}}
                disabled={!this.state.text}
                onClick={() => {this.onSearch()}}>
                <ArrowForwardIcon />
            </Button>
            </div>
            <Keyboard onKeyClicked={(e) => {this.onKeyboardClicked(e)}}>
            </Keyboard>
            </div>
            }
            </div>
            </ThemeProvider>
        );
    }
}

export default App;
